import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { NationalityComponent } from "./components/nationality/nationality.component";
import { CountryComponent } from "./components/country/country.component";
import { EducationDegreeComponent } from "./components/education-degree/education-degree.component";
import { ContractTypeComponent } from "./components/contract-type/contract-type.component";
import { ReligionComponent } from "./components/religion/religion.component";
import { InsuranceOfficeComponent } from "./components/insurance-office/insurance-office.component";
import { InstitutionComponent } from "./components/institution/institution.component";
import { KpiComponent } from "./components/kpi/kpi.component";
import { AddBankComponent } from "./components/bank/add-bank/add-bank.component";
import { BankIndexComponent } from "./components/bank/bank-index/bank-index.component";
import { IdentityTypeComponent } from "./components/identity-type/identity-type.component";
import { HireDocumentsComponent } from "./components/documents/hire-documents/hire-documents.component";
import { AssignAttachmentToJobComponent } from "./components/documents/assign-attachment-to-job/assign-attachment-to-job.component";
import { GovernorateComponent } from "./components/governorate/governorate.component";
import { FinancialCompanyComponent } from "./components/finincial-company/financial-company.component";
import { ViewsManager } from "app/shared/resources/views-manager";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ContactTypeComponent } from "./components/contact-type/contact-type.component";
import { ContactCategoryComponent } from "./components/contact-category/contact-category.component";

const routes: Routes = [
	{
		path: "nationality",
		component: NationalityComponent,
		data: { view: ViewsManager.nationality },
	},

	{
		path: "country",
		component: CountryComponent,
		data: { view: ViewsManager.country },
	},

	{
		path: "education-degree",
		component: EducationDegreeComponent,
		data: { view: ViewsManager.education_degree },
	},

	{
		path: "contract-type",
		component: ContractTypeComponent,
		data: { view: ViewsManager.contract_type },
	},
	{
		path: "contact-type",
		component: ContactTypeComponent,
		data: { view: ViewsManager.contact_type },
	},
	{
		path: "contact-category",
		component: ContactCategoryComponent,
		data: { view: ViewsManager.contact_category },
	},
	{
		path: "religion",
		component: ReligionComponent,
		data: { view: ViewsManager.religion },
	},
	{
		path: "finincial-company",
		component: FinancialCompanyComponent,
		data: { view: ViewsManager.finincial_company },
	},
	{
		path: "insurance-office",
		component: InsuranceOfficeComponent,
		data: { view: ViewsManager.insurance_office },
	},
	{
		path: "institution",
		component: InstitutionComponent,
		data: { view: ViewsManager.institution },
	},
	{
		path: "bank",
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},

			{
				path: "index",
				component: BankIndexComponent,
				data: { view: ViewsManager.bank_index },
			},

			{
				path: "add",
				component: AddBankComponent,
				data: { view: ViewsManager.add_bank },
			},

			{
				path: "edit/:bankid",
				component: AddBankComponent,
				data: { view: ViewsManager.add_bank },
			},
		],
	},
	{
		path: "hire-documents",
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},

			{
				path: "index",
				component: HireDocumentsComponent,
				data: { view: ViewsManager.hire_documents },
			},

			{
				path: "assign-to-job",
				component: AssignAttachmentToJobComponent,
				data: { view: ViewsManager.assign_to_job },
			},
		],
	},

	{
		path: "governorate",
		component: GovernorateComponent,
		data: { view: ViewsManager.governorate },
	},

	{
		path: "kpi",
		component: KpiComponent,
		data: { view: ViewsManager.kpi },
	},

	{
		path: "identity-type",
		component: IdentityTypeComponent,
		data: { view: ViewsManager.identity_type },
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class HrSetupRoutingModule {}
