import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import {
  IDocJobTitleModel,
  IDocJobTitleRequest,
} from "app/hr-setup/models/hire-documents";
import { db } from "app/shared/resources/database-tabels";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class AssignDocToJobtitleService extends AppClientService {
  constructor() {
    super();
  }

  create(body: IDocJobTitleRequest) {
    return this.post<IResponseList<IDocJobTitleModel[]>>(
      "HRSetup/JobTitleHireDocument/Create",
      body,

    );
  }

  getList(params:IPageParams) {
    return this.get<IResponseList<IDocJobTitleModel[]>>(
      "HRSetup/JobTitleHireDocument/GetList",
      {
        params: {
          ...params,
          $expand: `${db.Comm_Attachment_Type_Services}.${db.Comm_Attachment_Type},${db.Core_Job_Title}`,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IDocJobTitleModel[]>>(
      "HRSetup/JobTitleHireDocument/GetAll"

    );
  }

  Delete(param) {
    return this.delete<IResponse>("HRSetup/JobTitleHireDocument/Delete", {
          params:param
        });
  }
}
