import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IFinancialCompanyModel, IFinancialCompanyRequest } from "../models/financial-company";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";
@Injectable({
	providedIn: "root",
})
export class FinancialCompanyService extends AppClientService {
	constructor() {
		super();
	}

	create(body: IFinancialCompanyRequest) {
		return this.post<IResponse<IFinancialCompanyModel[]>>("HRSetup/FinancialCompany/Create", body);
	}

	update(body: IFinancialCompanyRequest) {
		return this.post<IResponse<IFinancialCompanyModel[]>>("HRSetup/FinancialCompany/Edit", body);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IFinancialCompanyModel[]>>("HRSetup/FinancialCompany/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IFinancialCompanyModel[]>>("HRSetup/FinancialCompany/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>(`HRSetup/FinancialCompany/Delete`, {
			params: { id: id },
		});
	}
}
